"use strict";
window.addEventListener("DOMContentLoaded", () => {

    /*ACCORDION*/
    const accordion = document.querySelector(".questions__box");
    const accordionItem = document.querySelectorAll(".questions__quest");
    const accordionBody = document.querySelectorAll(".questions__answer");

    function hideAccordionContent() {
        accordionBody.forEach(function (item) {
            item.classList.add("hide");
            item.classList.remove("show", "fade");
        });

        accordionItem.forEach(function (item) {
            item.classList.remove("text__color");
        });
    }

    function showAccordionContent(i = 0) {
        accordionBody[i].classList.add("show", "fade");
        accordionBody[i].classList.remove("hide");
        accordionItem[i].classList.add("text__color");
    }

    hideAccordionContent();
    showAccordionContent();

    accordion.addEventListener("click", function (event) {
        const target = event.target;

        if (target && target.classList.contains("questions__quest")) {
            accordionItem.forEach(function (item, index) {
                if (target === item) {
                    hideAccordionContent();
                    showAccordionContent(index);
                }
            });
        }
    });
    /*END ACCORDION*/
})



