"use strict";
window.addEventListener("DOMContentLoaded", () => {
    /*MOB MENU*/
    const mobBtn = document.querySelector(
        ".mobmenu__btn, mobmenu__btn::after, mobmenu__btn::before"
    );
    const mobMenu = document.querySelector(".mobmenu");

    mobBtn.addEventListener("click", function () {
        mobMenu.classList.toggle("mobmenu__show");
    });
    /*END MOBMENU*/

    /*ACCORDION
    function showAccordion(btn){
        const accordionBtn = document.querySelectorAll(btn);
    
        accordionBtn.forEach(function(item){
            item.addEventListener('click', function(){
                this.classList.toggle('text__color')
                this.nextElementSibling.classList.toggle('show');
            })
        })
    }
    showAccordion('.questions__quest');*/

    /*MODAL*/
    function modalClose(modalSelector) {
        const modal = document.querySelector(modalSelector);
        modal.classList.remove("show", "fade");
        modal.classList.add("hide");
        document.body.style.overflow = "";
    }

    function openModal(modalSelector, modalTimerId) {
        const modal = document.querySelector(modalSelector);
        modal.classList.remove("hide");
        modal.classList.add("show", "fade");
        document.body.style.overflow = "hidden";
        if (modalTimerId) {
            clearInterval(modalTimerId);
        }
    }

    function modal(triggerSelector, modalSelector, modalTimerId) {
        const modal = document.querySelector(modalSelector);
        const showModal = document.querySelectorAll(triggerSelector);

        showModal.forEach(function (event) {
            event.addEventListener("click", () =>
                openModal(modalSelector, modalTimerId)
            );
        });

        //Закрытие окна по клику вне кнопки
        modal.addEventListener("click", function (event) {
            if (
                event.target === modal ||
                event.target.getAttribute("data-close") == ""
            ) {
                modalClose(modalSelector);
            }
        });

        //Закрытие модального окна по клавише Esc
        document.addEventListener("keydown", function (e) {
            if (e.code === "Escape" && modal.classList.contains("show")) {
                modalClose();
            }
        });
    }

    const modalTimerId = setTimeout(
        () => openModal(".modal", modalTimerId),
        60000
    );
    modal("[data-modal]", ".modal", modalTimerId);
    /*END MODAL*/


    /*MAIL*/
    $(document).ready(function () {
        //E-mail Ajax Send
        $("form").submit(function () {
            //Change
            var th = $(this);
            $.ajax({
                type: "POST",
                url: "mail.php", //Change
                data: th.serialize(),
            }).done(function () {
                document.querySelector(".modal-2").classList.remove("hide");
                setTimeout(function () {
                    document.querySelector(".modal-2").classList.add("hide");
                    th.trigger("reset");
                }, 4000);
            });
            return false;
        });
    });
    /*END MAIL*/


    //SLIDER
    const dots1 = document.querySelector("#slick-slide-control00");

    $(document).ready(function () {
        $(".slider").slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 5000,
            nextArrow: '<div class="slider__btn-next"><img class="slider__btn-img" src="./img/next-btn.svg" alt=""></div>',
            prevArrow: '<div class="slider__btn-prew"><img class="slider__btn-img" src="./img/prew-btn.svg" alt=""></div>'
        });
    });
    /*END SLIDER*/

    /*CALC*/
    function calcCost(){
        let numval = document.querySelector('.calc__input').value;
        numval = parseInt(numval);
        let result = numval * 300 * 0.9;
        document.querySelector('.calc__price-area').innerHTML = result + ' злотих';    
        document.querySelector('.calc__input').value = ' ';
    }
    
    const calcBtn = document.querySelector('.calc__btn');
    calcBtn.addEventListener('click', calcCost);
    /*END CALC*/

});
